*{
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.todoHolder{
  height:75vh ;
  overflow: scroll;
  scroll-behavior: smooth;
}
::-webkit-scrollbar{
  display: none;
}

.center{
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}
.list{
  background: #00b4d8;
  border-radius:.2rem;
  overflow: hidden;
  cursor: pointer;
  color:#fff;
  transition:background .5s;
  user-select: none;
}

.list:hover{
  background: #0096c7;
}

.list input{
  background: transparent;
  font-size:1rem;
  color:#fff;
  border:none;
}

.list input:focus{
  outline:none;
}
